<div class="hello"><div class="ui container" v-if="users &amp;&amp; toList(users).length &gt; 0"><div class="ui grid"><div class="row ui form" v-show="uid"><div class="field"><div class="ui labeled input"><label class="ui label">輸入名字</label><input type="text" v-model="newName" placeholder="請先輸入社團名"></div></div><div class="field"><a class="ui green button" :class="{disabled: !newName}" @click="addGroup()">創建社團</a></div></div><div class="ui two column row"><div class="ui column ui segment" v-for="(g, idx) in searchBy(groups, mySearch)" :key="g.idx"><h3>〈{{g.n}}〉</h3><p>{{g.intro}}<br class="thin-only">&nbsp;&nbsp;&nbsp;&nbsp;<a @click="edit = !edit"><i class="edit icon"></i>{{edit ? '結束' : ''}}編輯社團資料</a></p><p><router-link class="ui basic green button" :to="'/group/' + g.idx"><i class="sign-in icon"></i>前往社團</router-link></p><div class="ui form" v-show="edit"><div class="field" v-if="!uid"><button class="ui orange button" @click="loginGoogle()"><i class="google icon"></i>請先登入</button></div><div class="field"><div class="ui labeled input"><label class="ui label">輸入社團簡介  </label><input type="text" v-model="newIntro" placeholder="請先輸入社團簡介"></div></div><div class="field"><a class="ui green button" :class="{disabled: !newIntro}" @click="addIntro(idx)">更新簡介</a></div></div><div class="ui grid"><div class="row"><p>成員：<span v-for="m in g.members"><router-link :to="'/flag/' + m" v-if="users[m]"><img class="ui avatar" :src="users[m].photoURL" alt="users[m].n"></router-link></span><span v-show="uid"><a class="ui green tiny button" v-show="!isMember(g.idx)" @click="join(g.idx)">我要加入</a><a class="ui red tiny button" v-show="isMember(g.idx)" @click="out(g.idx)">我要退出</a></span></p></div><div class="two column stackable row"><div class="column"><div class="ui divided list"><div class="item left aligned">資源</div><div class="item left aligned" v-for="(r, index) in g.res" :key="index + r.n + r.href"><a :href="r.href" target="_blank" rel="noopener noreferrer"><img :src="'http://www.google.com/s2/favicons?domain=' + r.href" :alt="r.n">{{r.n}}</a></div><div class="item ui form" v-show="uid &amp;&amp; edit"><div class="field"><div class="ui labeled input"><label class="ui label">輸入資源名</label><input type="text" v-model="newResName" placeholder="請先輸入資源名"></div></div><div class="field"><div class="ui labeled input"><label class="ui label">輸入資源網址</label><input type="text" v-model="newHref" placeholder="請先輸入資源網址"></div></div><div class="field"><a class="ui green button" :class="{disabled: !newHref || !newResName}" @click="addRes(idx)">新增資源</a></div></div></div></div><div class="column"><div class="ui divided list"><div class="item" v-for="(c, index) in g.chats" :key="index">{{c.n}} : {{c.t}}</div><div class="item ui form" v-if="uid &amp;&amp; edit"><div class="field"><img class="ui avatar" :src="photoURL"><div class="ui labeled input"><input class="input" v-model="msg" placeholder="在想什麼嗎?"><a class="ui label green button" :class="{disabled: !msg}" @click="addChat(idx)">留言</a></div></div></div></div></div></div></div></div></div></div></div></div>